.Blog {
  width: 380px;
  max-height: 555px;
  background-color: rgba(16, 20, 24, 0.7);
}

.Blog img {
    width: 380px;

}
.Blog .blog-content {
  box-sizing: border-box;
  padding: 39px 64px 80px 44px;
}

.Blog .blog-content h3 {
  font-weight: 700;
  font-size: 22px;
  line-height: 26px;
  text-transform: capitalize;
  margin-bottom: 17px;
}

.Blog .blog-content p {
  font-weight: 400;
  font-size: 16px;
  line-height: 25px;
  text-transform: capitalize;
  margin-bottom: 21px;
}

.Blog .blog-content a {
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 0.13em;
  text-transform: capitalize;
  color: inherit;
  text-decoration:none ;
  padding-bottom: 4.5px;
  border-bottom: 1px solid #FFCE6D;

}
