.Card {
  box-sizing: border-box;
  width: 384px;
  height: 455px;
  background: rgba(160, 128, 64, 0.3);
  border: 1px dashed #ffce6d;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 58px;
  gap: 0;
}

.Card img {
  margin-top: 73px;
  margin-bottom: 17px;
}

.Card h4 {
  font-size: 24px;
  line-height: 28px;
  text-transform: capitalize;
  margin-bottom: 17px;
}

.Card p {
font-weight: 400;
font-size: 14px;
line-height: 22px;
text-align: justify;
letter-spacing: 0.13em;
text-transform: capitalize;
}
