.Info {
  background: center center no-repeat;
  background-image: url("../../images/black-bg.png");
  color: #fffffc;
    background-size: cover;

}

.Info .overlay {
  box-sizing: border-box;
  background: rgba(16, 20, 24, 0.7);
  height: 375px;
  padding-top: 88px;
  padding-bottom: 74px;
}

.Info .overlay .container {
  width: 71%;
}

.Info .overlay .container .boxes {
  display: flex;
  gap: 57px;
}


.Info .overlay .container .boxes h4{
font-size: 20px;
line-height: 23px;
text-transform: capitalize;
margin-bottom: 30px;
}


.Info .overlay .container .boxes .left .logo-name {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 19px;
}

.Info .overlay .container .boxes .left .logo-name h3 {
  font-size: 24px;
  line-height: 28px;
}

.Info .overlay .container .boxes .left p {
  font-weight: 400;
  font-size: 16px;
  line-height: 25px;
  letter-spacing: 0.13em;
  text-transform: capitalize;
  width: 302px;
  margin-bottom: 18px;
}

.Info .overlay .container .boxes .left ul{
  list-style: none;
  display: flex;
  gap: 14px;

}

.Info .overlay .container .boxes .mid ,
.Info .overlay .container .boxes .right {
padding-top: 27px;
}


.Info .overlay .container .boxes .mid ul,
.Info .overlay .container .boxes .right ul {
margin-left: 7px;

}
.Info .overlay .container .boxes .mid ul li{
margin-bottom: 7px;

}

.Info .overlay .container .boxes .right ul {
  list-style: none;
    display: flex;
  flex-direction: column;
  gap: 18px;
}

.Info .overlay .container .boxes .right ul li {
    display: flex;
    align-items: flex-start;
    gap: 15px;
}
.Info .overlay .container .boxes .right ul li img{
  width: 25px;
  margin-top: 4px;

}
.Info .overlay .container .boxes .right ul li p {
  width: 290px;
font-weight: 400;
font-size: 16px;
line-height: 25px;
letter-spacing: 0.13em;
text-transform: capitalize;
}